<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <select class="menuSelect" @change="selectItemById(), getOptionText($event)" id="" v-model="art">
      <option v-for="banda in bandas" :key="banda.id" :value="banda.id">{{ banda.nome }}</option>
    </select>
    <select class="menuSelect"  name="" id="" v-model="mus">
      <option selected v-for="musica in filteredItems" :key="musica.idBanda" :value="musica.nome">{{ musica.nome }}</option>
    </select>
    <textarea v-model="musica" disabled class="textAreaclass" name="observacao" cols="40" rows="30">
   
    </textarea>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'HelloWorld',
  props: {

    msg: String

  },
  data() {
    return{

      key:'3e83e523ef18a449f4eed36e9291ceae',
      bandas:[
        {id: 0, nome: 'oficina-g3'},
        {id: 1, nome: 'aline-barros'}, 
        {id: 2, nome: 'resgate'},
      ],
      musicas:[
        {idBanda: 0, nome: 'a ele'},
        {idBanda: 1, nome: 'ressucita-me'}, 
        {idBanda: 2, nome: 'todo som'},
      ],
      selectedItem: {},
      art:'',
      artista:'',
      mus:'',
      musica:'',
      x:'',
      y:'',

    }
  },
  computed: {

  filteredItems() {

    return this.musicas.filter(item => item.idBanda === this.art)

  }

  },
  filters: {
    upper: function (value) {
      return value.toUpperCase();
    },
    dataFormatada: function (value) {
      return value.split("-").reverse().join("/");
    },
    duasCasasDecimais: function (value) {
      return Number(value).toFixed(2);
    },
  },
  methods: {
    getOptionText(event) {
      const selectedOptionIndex = event.target.options.selectedIndex;
      this.artista = event.target.options[selectedOptionIndex].text;
      this.x = event.target.options[selectedOptionIndex].value;
    },
    selectItemById() {
      this.selectedItem = this.musicas.find(item => item.idBanda === this.art);
      console.log(this.selectedItem)
    },
    getAllMeds(){
      axios
        .get(
        `https://api.vagalume.com.br/search.php?apikey=${this.key}&art=${this.artista}&mus=${this.mus}`,
   
      )
      // eslint-disable-next-line
      .then((res) => {
          this.musica = (res.data.mus[0]['text'])
          console.log(res.data.mus[0]['text'])
        })
        .catch((err) => {
          console.log(err);
        });
    },


},
watch:{
  mus(){
    this.getAllMeds()
  },
  art(){
    this.getAllMeds()
  },
},
mounted() {

this.getAllMeds()


},
}
</script>

